<template>
  <div class="staticReports">
    <el-container>
      <el-header class="minWidth">
        <div class="staticReports minWidth loadingtext"
             v-loading="imgLoading"
             element-loading-text="加载中..."
             element-loading-background="rgba(255, 255, 255, 0.8)">
          <el-row class="staticReports-header">
            <el-col :span="17"><span class="mz" @click="$router.back(-1)">流程报表</span> <i class="el-icon-arrow-down"></i>
            </el-col>
            <el-col :span="7" class="statisticjobbtns">
              <el-button @click="inquire" type="primary" size="mini" round>查询
              </el-button>
              <el-button @click="refresh" type="primary" size="mini" round>重置
              </el-button>
            </el-col>
          </el-row>
          <div class="taticReports-content">
            <!--<div class="resulter">报表条件
              <div class="fl fz12 curpoint" @click="showToggle"><span v-text="arrt"></span> <i :class="icon"></i></div>
            </div>-->
            <div class="statement " v-if="isShow">
              <el-row style="margin-bottom: 10px">
                <el-col :span="24">
                  <span >静态-统计某个人员参与的流程报表</span>
                </el-col>
              </el-row>
              <el-row class="staticReports-middle">
                <el-col :sm="4" :md="1" style="min-width: 78px;"><span>账号选择</span></el-col>
                <el-col :span="7" class="accountchiose">
                    <span @dblclick="openDialog('originator_dailog')">
                       <i class="iconfont icon-member" @click="openDialog('originator_dailog')"></i>
                        <el-select
                          v-model="model.userName[0]"
                          filterable
                          clearable
                          @clear="emptyselect"
                          remote
                          reserve-keyword
                          placeholder=""
                          popper-class="orgstates"
                          :remote-method="querySearchUser"
                          value-key='userId'
                          @change = "personChange"
                          @remove-tag = "removeUser"
                          @visible-change="visibleChange"
                          :loading="loading"
                          ref="userId">
                          <el-option
                            v-for="(item,index) in orgStates"
                            :key="index"
                            :label="item.value"
                            :value="item">
                            <div class="userheight">{{item.value}}({{item.userId}})</div>
                            <div class="fullpathsize">{{item.orgFullPath}}</div>
                          </el-option>
                        </el-select>
                      <!-- <el-input v-model="model.userName" placeholder="" :fetch-suggestions="querySearchUser"
                          :disabled="true" size="mini"></el-input> -->
                    </span>
                </el-col>
                <!--<el-col :span="1">-->
                 <!---->
                <!--</el-col>-->
                <el-col :span="5"><i  :class="[hintwringShow?'el-icon-warning':'']"></i><span>{{hintwring}}</span></el-col>
                <el-col :span="3">
                  <el-checkbox @change="isChange" v-model="isAvailable">包含无效流程</el-checkbox>
                </el-col>

              </el-row>
              <el-row>
                <el-col :span="24" style="text-align:right; float: right;">

                  <el-button @click="exportList" class="upload" type="primary" round icon="iconfont icon-export"
                             size="mini">导出
                  </el-button>
                </el-col>
              </el-row>
            </div>
          </div>

          <div class="taticReports-content">
            <div class="statement">
              <el-table
                ref="singleTable"
                :data="tableData"
                border
                highlight-current-row
                :height="isShow?fullHeight-305:fullHeight-317"
                style="width: 100%;margin-bottom:30px;"
                @sort-change="sortChange">
                <el-table-column
                  prop="orgName"
                  sortable='custom'
                  label="公司">
                </el-table-column>
                <el-table-column
                  prop="processFullName"
                  label="目录"
                  align="left"
                  sortable='custom'
                  min-width="155">
                </el-table-column>
                <el-table-column
                  prop="processID"
                  min-width="50"
                  sortable='custom'
                  label="ID">
                </el-table-column>
                <el-table-column
                  prop="processName"
                  min-with="180px"
                  sortable='custom'
                  label="流程名称">
                </el-table-column>
                <el-table-column
                  prop="templeName"
                  sortable='custom'
                  label="模板名称">

                </el-table-column>
                <el-table-column
                  prop="stepInfos"
                  min-width="150"
                  align="left"
                  sortable='custom'
                  label="步骤信息">
                </el-table-column>
                <el-table-column
                  prop="processCCUser"
                  min-width="100"
                  sortable='custom'
                  label="抄送人">
                </el-table-column>
                <el-table-column
                  prop="isAvailabletype"
                  sortable='custom'
                  label="是否有效">
                </el-table-column>
                <div slot="empty" v-show="!imgLoading">
                  <p :style="{'marginTop': '23px'}">暂无数据</p>
                </div>
                <div slot="append"> 
                    <!--分页-->
                    <page :pageIndex="pageIndex" :total="total" :pageSize="pageSize" :pageSizesList="pageSizesList"
                    @handleSizeChange="handleSizeChange" @handleCurrentChange="handleCurrentChange"></page>
                  </div>
              </el-table>
              
            </div>
          </div>
        </div>
        <component :is="comp.component" @hideComponent="hideComponent" v-if="showdialog" @sureSubmit="showMsgfromchild"
                   v-bind:oneCheck="oneCheck"
                   v-bind:childDataUrl="childDataUrl"
                   v-bind:queryName="queryName"
        >
        </component>
      </el-header>

    </el-container>
  </div>
</template>
<script>
  import page from "../plugins/page/index";
  import util from '../js/util.js'
  import orginatordialog from '../components/dailog/originator_dailog'
  import projectdialog from '../components/dailog/project_dialog'
  import processdialog from '../components/dailog/process_dialog'
  import loading from '../plugins/loading/loading'

  export default {
    name: 'staticsJob',
    components: {
      page,
      originator_dailog: orginatordialog,
      project_dialog: projectdialog,
      process_dialog: processdialog
    },
    mounted() {
      var that = this;
      //that.getList();
    },

    data() {
      return {
        fullHeight: document.documentElement.clientHeight, //获取文档可视化高度
        isShow: true,//头部收起
        queryName:'',//查询字段
        multiplelimit:1,//限制选人数量
        loading:false,
        arrt: '收起',//初始动画
        icon: 'el-icon-arrow-up',//初始动画图标
        isAvailable: false,//是否有效
        model: {
          selectForm: {
            userID:'',
            poisitionID :null,
            processIDs:'',
            processCategoryIDs:''
          },
          userName: []
        },
        hintwring:'',//提示信息
        hintwringShow:false,//提示信息是否出现
        tableData: [],//初始table
        orgStates: [],
        processList: [],//分页
        pageSizesList: [10, 20, 50, 100], //点击分页信息
        pageIndex: 1,//当前页码
        pageSize: 10,//当前页数
        total: 0,//总数
        showdialog: false,//弹窗初始样式
        comp: {},//初始弹窗使用的对象

        //传给子组件的值
        exportData: [],
        oneCheck:true,
        orderBy:'',
        checked:'1',
        childDataUrl: {
          url: '',
          selectedPerson:[],
          chooseProject: {}
        },//传给子组件的值
        // 加载动画
        imgLoading: false
      }
    },
    methods: {
      // 输入框清空
      emptyselect(){
        this.childDataUrl.selectedPerson=[];
        this.model.userName=[]
      },


      /*
     * 是否选中状态  0是无效 1是有效
     * */
      isChange(e) {
        this.isAvailable = e;
        // if (this.isAvailable === '') {
        //   this.checked = ''
        // }
        if (this.isAvailable) {
          this.checked = '';
        } else {
          this.checked = '1';
        }
      },

      /*
      * 头部收起展开
      * */
      showToggle() {
        this.isShow = !this.isShow;
        if (this.isShow) {
          this.arrt = "收起";
          this.icon = "el-icon-arrow-up"
        } else {
          this.arrt = "展开";
          this.icon = "el-icon-arrow-down"
        }
      },

      //表格排序
      sortChange(column) {
        this.orderBy = `${(column.prop=='isAvailabletype')?'isAvailable':column.prop} ${column.order.replace('ending', '')}`;
        if(this.model.userName.length !== 0) {
          this.getList();
        }
      },
      //查询按钮搜索
      search() {
        this.pageIndex = 1;
        this.getList();

      },
      /*
      * 分页逻辑
      * */
      handleSizeChange(val) {
        var pageSize = `${val}`;
        this.pageIndex = 1;
        this.pageSize = parseInt(pageSize);
        localStorage.checkPageSize = val;
        this.getList()
      },
      handleCurrentChange(val) {
        this.pageIndex = val;
        this.getList();

      },
      /*
      * 表格数据渲染
      * */
      getList() {
        // loading.startLoading();
        this.imgLoading = true;
        this.model.selectForm.orderBy = this.orderBy;
        this.model.selectForm.isAvailable = this.checked;
        this.model.selectForm.pageSize = this.pageSize;
        this.model.selectForm.pageIndex = this.pageIndex-1;

        // 判断是否存在localStorage

        if(localStorage.todoPageSize) {
          this.pageSize = Number(localStorage.todoPageSize)
        }
        let userId = '';
        this.model.selectForm.userID='';
        for (let origina of this.childDataUrl.selectedPerson) {
          userId += `VANKE\\${origina.userId};`;
        }
        //选择发起人
        if (userId) {
          this.model.selectForm.userID = userId.substring(0, userId.length - 1);
        }
        this.$axios.post('/api/reportForm/peopleApprovedProcesses',this.model.selectForm).then((result) => {
          this.tableData = result.data.processList;
          this.total = result.data.listCounts;
          for (let i = 0; i < result.data.processList.length; i++) {
            switch (Number(result.data.processList[i].isAvailable)) {
              case 0:
                result.data.processList[i].isAvailabletype = '否';
                break;
              case 1:
                result.data.processList[i].isAvailabletype = '是';
                break;
              default:
                result.data.processList[i].isAvailabletype = '否';
            }
          }
          // loading.endLoading();
          this.imgLoading = false;
        }).catch((err) => {
          // loading.endLoading();
          this.imgLoading = false;
          this.$message.error(`查询列表数据失败!ERROR MESSAGE:${err}`);
        })
      },
      /*
      * 数据处理
      * */

      /*
   * 重置数据
   * */

      inquire(){
        this.pageIndex= 1;
        if(this.model.userName.length == 0){
          this.$message.warning("请选择账号!");
        }else {
          this.getList()
        }
      },
      refresh() {
        this.model.selectForm.searchWord = '';
        this.model.selectForm.companys = '';
        this.isAvailable = '';
        this.checked = '';
        //所属部门
        this.$nextTick(() => {
          this.model.selectForm.userID ='';
          this.model.userName = [];//发起人
          this.model.processFullName = '';//流程类别
          this.model.Date = [];//日期
          this.childDataUrl.selectedPerson=[];//清空回填

        });
      },
      toTree(data) {
        if(data==null || data.length==0) return [];
        data.forEach(function (item) {
          delete item.children;
        });
        var map = {};
        data.forEach(function (item) {
          map[item.id] = item;
        });
        var val = [];
        data.forEach(function (item) {
          var parent = map[item.pid];
          if (parent) {
            (parent.children || ( parent.children = [] )).push(item);
          } else {
            val.push(item);
          }
        });

        return val;
      },
      /*
      导出
      */

      exportList() {
        loading.startLoading({
          lock: true,
          text: '数据量过大，正在拼命加载中...',
          background: 'rgba(255, 255, 255, 0.9)',
          target: document.querySelector('.loadingtext')
        });
        let obj={
          "params":{
            "pageIndex":0,
            "pageSize":this.total,
            "orderBy": this.orderBy,
            "isAvailable": this.checked,
            "userID":this.model.selectForm.userID,
            "processIDs": "",
            "processCategoryIDs":"",
            "poisitionID":null
          },
          "tableName": "静态统计某个岗位参与的流程",
        };
        this.$axios.post('/api/exportExcelForTable', obj, {responseType: 'blob'}).then((result) => {
          loading.endLoading();
          const content = result.data;
          const dowLoadFileName = '静态统计某个岗位参与的流程';
          util.exportSearchList(dowLoadFileName, content)

        }).catch((err) => {
          loading.endLoading();
          this.$message.error(`导出失败!ERROR MESSAGE:${err}`);
        })
      },

      /**
       * 获取所有人员
       *
       */
      getUsers(userName) {
        let that = this;
        that.orgStates=[];
        that.$axios.post('/api/getUser',{'name': userName, 'deptId': '','isWork':'1'}).then((result) => {
          for (var i = 0; i < result.data.result.length; i++) {
            that.orgStates.push({
              'value':result.data.result[i].displayName,
              'userId':result.data.result[i].adAccount,
              'orgFullPath': result.data.result[i].companyName+'-'+ result.data.result[i].orgNameFullPath,
              'vankeId':result.data.result[i].vankeId//添加新增vankId
            });
          }
          this.loading = false;
        }).catch(err => {
          that.$message.error(`查询列表数据失败!ERROR MESSAGE:${err}`);
        });

      },
      /**
       * 发起人模糊搜索
       */
      querySearchUser(userName) {
        this.orgStates=[];
        this.queryName = userName;
        if(this.model.userName.length==0){
          this.hintwring ='';
          this.hintwringShow = false;
          var that = this;
          this.loading = true;
          if (this.isSeach) {
            return;
          }
          this.isSeach = true;
          setTimeout(() => {
            that.isSeach = false;
            that.validationInput(that.queryName);
          }, 2000);
        }else if( userName.trim() ==''){
          this.hintwringShow = false;
          this.hintwring ='';
        }else{
          this.hintwringShow = true;
          this.hintwring = '注意：此处账号选择只能单选！';
        }
      },
      // 发起人选择改变
      personChange (data) {
        this.$set(this.$refs.userId,'query','');//选中后清空输入的文本
        this.childDataUrl.selectedPerson[0] = data;
      },
      //删除用户
      removeUser(data){
        if (data.userId) {
          for (var it = 0; it < this.childDataUrl.selectedPerson.length; it++) {
            if (data.userId == this.childDataUrl.selectedPerson[it].userId) {
              break;
            }
          }
          this.childDataUrl.selectedPerson.splice(it, 1);
        } else {
          for (var it = 0; it < this.childDataUrl.selectedPerson.length; it++) {
            if (data == this.childDataUrl.selectedPerson[it].value) {
              break;
            }
          }
          this.childDataUrl.selectedPerson.splice(it, 1);
        }
      },
      //验证延时后的值是否跟当前文本框值相同

      validationInput(userName) {
        if(userName=='' || userName.length==0){
          this.loading = false;
          return null;
        }
        if(userName == this.$refs.userId.query){
          this.getUsers(userName);
        }else{
          this.orgStates=[];
        }
      },
      // 下拉隐藏时候隐藏验证提示
      visibleChange(val){
        this.orgStates = [];
        if(!val){
          this.hintwringShow = false;
          this.hintwring ='';
        }
      },
      clearUser(){
        this.childDataUrl.selectedPerson=[];
      },

      /**
       * 弹窗控件
       */
      openDialog(type, url) {
        // 弹出框类型
        this.comp.component = type;
        this.showdialog = true;         util.showDailog_mask();//解决ie浏览器问题显示遮罩
        if (type == 'originator_dailog') {//发起人
          this.oneCheck = true;
          this.childDataUrl.getTree = 'getTree';
          this.childDataUrl.companytree = 'companytree/page';
        } else if (type == 'project_dialog') {//组织
          this.childDataUrl.getTree = 'getTree';
          this.childDataUrl.companytree = 'companytree/page';
        }
      },
      /**
       * 弹窗组件带出数据赋值给主组件
       */
      showMsgfromchild(data) {
        if (data.type && data.type == "project") {
          //选择组织架构
          // let txt = (data.item.orgName == undefined) ? data.last.lastNode : data.last.lastNode + "-" + data.item.orgName;
          if (this.oneCheck) {
            let txt = data.last.lastNode;
            this.model.companys_t = txt;
            this.model.companys = txt;
            if(this.model.companys.length > 15){
              this.model.companys = this.model.companys.substr(0,20)+'...';
            }
            this.model.selectForm.departmentID = data.item.orgID;
            this.model.selectForm.companyCode = data.last.lastId.toString();
          } else {
            var chooseDatas = data.items;
          }
        } else if (data.type && data.type == "process") {
          //选择流程类别
          this.processFormName = data.text;
          this.model.selectForm.processID = data.processID;
        } else if (data.type && data.type == "originator") { // 选择发起人
          this.childDataUrl.selectedPerson = [];
          this.model.userName = [];
          for (let origina of data.peoArr) {
            var userId =origina.userId;
            var vankeId =origina.vankeId
            var bool = false;
            for (var j = 0; j < this.childDataUrl.selectedPerson.length; j++) {
              if (this.childDataUrl.selectedPerson[j].userId === userId) {
                bool = true;
              }
            }
            if (!bool) {
              this.childDataUrl.selectedPerson.push({userId:userId,value:origina.name,vankeId:vankeId});
              this.model.userName.push(origina.name);
            }
          }
        }
        this.hideComponent();
      },
      /**
       * 关闭弹出框组件
       */
      hideComponent() {
        this.showdialog = false;
        util.hiddenDailog_mask();//解决ie浏览器问题 隐藏遮罩
      },

    },
    //点击页码实现翻页
    watch: {
      fullHeight() {
        return document.documentElement.clientHeight;
      }
    },
    computed: {}
  }
</script>
